import base from './base.js'

const base_url = 'https://tencentim.cdnis.edu.hk/icapp/'

const prod = Object.assign(base, {
  base_url
})

export default prod

export function transformHeadUrl (attachmentId, token) {
  if (useMockIntercept) {
    let imgs = window.sessionStorage.getItem('imgs')
    if (imgs) {
      imgs = JSON.parse(imgs)
      attachmentId = Number(attachmentId)
      for (let val of imgs) {
        if (val.attachmentId === attachmentId) {
          return val.path
        }
      }
    }
  }
  return `${ base_url }commont/getAttachment?attachmentId=${ attachmentId }&token=${ token }`
}
